.Editor-text {
  background-color: transparent!important;
  margin: 0;
  -webkit-transition: background-color 0.2s fade-in-out;
  transition: background-color 0.2s fade-in-out;
}
.Editor-text .text-content {
  cursor: default;
}
.edit.Editor-text .text-content {
  cursor: pointer;
}
.Editor-activeText.Editor-text .text-content {
  cursor: default;
}
.Editor-text .Editor-panel .Editor-textValue {
  border: 1px solid #e1e1e1;
  display: block;
  font-family: 'Inconsolata', 'Menlo', monospace;
  font-size: 14px;
  min-height: 300px;
  margin: 20px 0;
  outline: none;
  padding: 14px;
  box-sizing: border-box;
  width: 100%;
}
.Editor-text .Editor-panel .Editor-buttons {
  text-align: center;
}
.Editor-text .Editor-panel .Editor-saveButton,
.Editor-text .Editor-panel .Editor-removeButton {
  background-color: #fff;
  border: 1px solid #0a0;
  cursor: pointer;
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: 200;
  margin: 10px auto;
  padding: 6px;
  border-radius: 3px;
  width: 100px;
}
.Editor-text .Editor-panel .Editor-removeButton {
  border-color: #aaa;
  color: #999;
  margin-left: 8px;
}
.Editor-text .Editor-panel .Editor-invalidButton {
  background-color: #eee;
  border-color: #a00;
  color: #666;
}
.rdw-editor-wrapper .public-DraftEditorPlaceholder-root {
  padding: 10px 15px;
}
.rdw-editor-wrapper .DraftEditor-editorContainer .public-DraftEditor-content[contenteditable="true"] > div {
  min-height: 100px;
  padding: 10px 15px;
  border: 1px solid #d1d8dc;
  border-radius: 5px;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
}
.rdw-editor-wrapper .DraftEditor-editorContainer .public-DraftEditor-content[contenteditable="true"] > div:focus {
  border-color: #1aa3ff;
}
.rdw-editor-wrapper .DraftEditor-editorContainer .public-DraftEditor-content .public-DraftStyleDefault-block {
  margin: 0.3rem 0;
  font-size: 0.9rem;
}

/**
 * prism.js default theme for JavaScript, CSS and HTML
 * Based on dabblet (http://dabblet.com)
 * @author Lea Verou
 */

code[class*="language-"],
pre[class*="language-"] {
	color: black;
	background: none;
	text-shadow: 0 1px white;
	font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.5;

	-moz-tab-size: 4;
	tab-size: 4;

	-webkit-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
}

pre[class*="language-"]::-moz-selection, pre[class*="language-"] ::-moz-selection,
code[class*="language-"]::-moz-selection, code[class*="language-"] ::-moz-selection {
	text-shadow: none;
	background: #b3d4fc;
}

pre[class*="language-"]::selection, pre[class*="language-"] ::selection,
code[class*="language-"]::selection, code[class*="language-"] ::selection {
	text-shadow: none;
	background: #b3d4fc;
}

@media print {
	code[class*="language-"],
	pre[class*="language-"] {
		text-shadow: none;
	}
}

/* Code blocks */
pre[class*="language-"] {
	padding: 1em;
	margin: .5em 0;
	overflow: auto;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
	background: #f5f2f0;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
	padding: .1em;
	border-radius: .3em;
	white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
	color: slategray;
}

.token.punctuation {
	color: #999;
}

.namespace {
	opacity: .7;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
	color: #905;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
	color: #690;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
	color: #a67f59;
	background: hsla(0, 0%, 100%, .5);
}

.token.atrule,
.token.attr-value,
.token.keyword {
	color: #07a;
}

.token.function {
	color: #DD4A68;
}

.token.regex,
.token.important,
.token.variable {
	color: #e90;
}

.token.important,
.token.bold {
	font-weight: bold;
}
.token.italic {
	font-style: italic;
}

.token.entity {
	cursor: help;
}

/* Slider */
.slick-slider
{
    position: relative;

    display: block;
